// Components
import BaseHeader from '@/components/argon-core/BaseHeader'
import MainTitle from '~/components/shack/MainTitle.vue'
import SectionTitle from '~/components/shack/SectionTitle.vue'
import { mapState } from 'vuex'

export default {
  layout: 'ShackDash',
  async asyncData({ store, $event }) {
    await $event.getEventCategories().then(({ data }) => {
      store.commit('events/setCategories', data)
    })
  },
  components: {
    BaseHeader,
    MainTitle,
    SectionTitle
  },
  data: () => ({
    cat: null,
    name: null,
    color: null,
    disable: false
  }),
  computed: {
    ...mapState({
      categories: state => state.events.categories
    })
  },
  methods: {
    changeCategory(cat) {
      this.name = cat.name
      this.color = cat.color
      this.disable = cat.disable
    },
    updateCategory() {
      const { cat, name, color, disable } = this

      this.$event
        .updateCategory(cat, {
          name,
          color,
          disable
        })
        .then(res => {
          if (res.data) {
            this.$bvToast.toast('Event category updated successfully', {
              title: `Success`,
              variant: 'success',
              solid: true
            })
          }
        })
        .catch(({ response }) => {
          this.$bvToast.toast(JSON.stringify(response.data.errors), {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
        })
    },
    deleteCategory() {
      if (!confirm('Are you sure?')) return

      const { cat } = this
      this.$event
        .deleteCategory(cat)
        .then(res => {
          this.$store.commit('events/removeCategory', cat)
        })
        .catch(({ response }) => {
          this.$bvToast.toast(JSON.stringify(response.data.message), {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
        })
    }
  }
}
